import {useState} from 'react'
import {getAppLanguage, setAppLanguage} from '../utilities/storage'
import {ENGLISH_LANGUAGE_KEY, FARSI_LANGUAGE_KEY} from '../Constants'

const LanguageChanger = () => {
  const [language, setLanguage] = useState(getAppLanguage())

  const languageHandler = (input) => {
    setAppLanguage(input)
    setLanguage(input)
  }

  return (
    <div className='flex gap-1'>
      <button
        className={`bg-green-300 p-1 ${language === FARSI_LANGUAGE_KEY ? 'border' : ''}`}
        onClick={() => languageHandler(FARSI_LANGUAGE_KEY)}
      >
        Fa
      </button>
      <div>|</div>
      <button
        className={`bg-blue-300 p-1 ${language === ENGLISH_LANGUAGE_KEY ? 'border' : ''}`}
        onClick={() => languageHandler(ENGLISH_LANGUAGE_KEY)}
      >
        Eng
      </button>
    </div>
  )
}

export default LanguageChanger
